export default {
  data: function data() {
    return {
      cascaderValue: '',
      options: [{
        id: 1,
        label: 'Shopify',
        children: []
      }, {
        id: 2,
        label: 'Woocommerce',
        children: []
      }, {
        id: 3,
        label: 'Lazada',
        children: []
      }, {
        id: 4,
        label: 'Shopee',
        children: []
      }],
      visible: false,
      pageKey: new Date().valueOf(),
      activeName: "",
      fulfillArr: {
        fulfilled: {
          text: "Fulfilled",
          type: "info",
          r: 3
        },
        unfulfilled: {
          text: "Unfulfilled",
          type: "warning",
          r: 1
        },
        partial: {
          text: "Partially Fulfilled",
          type: "danger",
          r: 2
        },
        refunded: {
          text: "Refunded",
          type: "info",
          r: 3
        }
      },
      dateArr: {
        1: "Today",
        2: "Last 7 days",
        3: "Last 30 days",
        4: "Last 90 days",
        5: "Last 12 months",
        6: "Custom"
      },
      sortArr: {
        1: "Order number (ascending)",
        2: "Order number (descending)",
        3: "Date (oldest first)",
        4: "Date (newest first)"
      },
      paymentArr: {
        paid: {
          text: "Paid",
          type: "info",
          r: 3
        },
        partially_refunded: {
          text: "Partially refunded",
          type: "danger",
          r: 2
        },
        refunded: {
          text: "Refunded",
          type: "info",
          r: 3
        }
      },
      storeList: [],
      readyNum: 0,
      responsingNum: 0,
      waitNum: 0
    };
  },
  components: {},
  watch: {
    $route: function $route() {
      this.activeName = this.$route.name;
      this.getNum();
    }
  },
  mounted: function mounted() {
    this.activeName = this.$route.name;
    this.getNum();
    this.getStore();
  },
  methods: {
    quotation: function quotation() {
      this.visible = true;
    },
    getNum: function getNum() {
      var _this = this;

      //获取tabs数字
      this.$apiCall("api.OfferList.getNumbers", {}, function (r) {
        // console.log(r);
        if (r.ErrorCode == 9999) {
          var data = r.Data.Results;
          _this.readyNum = data.readyNum;
          _this.responsingNum = data.responsingNum;
          _this.waitNum = data.waitNum;
        }
      });
    },
    getStore: function getStore() {
      var _this2 = this;

      this.$apiCall("api.Invoice.findStores", {
        authStatus: 1
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this2.storeList = [];
          r.Data.Results.forEach(function (e) {
            _this2.storeList.push({
              shopifyAccountId: e.id,
              store: e.shopName
            });
          });

          _this2.options.map(function (item) {
            r.Data.Results.filter(function (a) {
              if (a.platform == item.id) {
                item.children.push({
                  label: a.shopName,
                  value: a.id
                });
              }

              ;
            });
          });
        } else {
          _this2.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    handleClick: function handleClick(tab) {
      if (tab.name == this.$route.name) {
        //点击相同路由刷新
        this.pageKey = new Date().valueOf();
      } else {
        //tabs 切换
        this.$router.push({
          name: tab.name
        });
      }
    }
  }
};